import React from 'react';

import {TextGroup, Important, Title, SubTitle, Line} from '../../components/TextUtils/TextUtils.js';

var version = "1.1.0";

var UpdateTitle = () => {
    return "TwitchLink " + version;
};

var UpdateNote = () => {
    return (
        <div>
            <SubTitle>업데이트 노트</SubTitle>
            <div style={{padding: "30px 10px"}}>
                <TextGroup>
                    <Title>개발 중</Title>
                    <Line/>
                    일부 장치 및 해상도에서 UI가 올바르게 표시되지 않는 문제를 해결하였습니다.
                    <br/>
                    이제 전체 화면 및 창 크기 조절이 가능합니다.
                    <br/>
                    다운로드 엔진을 개선하였습니다.
                    <br/>
                    이제 초고속 다운로드에 임시 저장용량이 필요하지 않습니다.
                    <br/>
                    파일 확장자 제한을 제거하였습니다.
                    <br/>
                    mp4 파일로 다운로드 도중 중단하면 파일이 재생되지 않는 문제를 해결하였습니다.
                    <br/>
                    이제 설정에서 음소거 해제 여부를 선택할 수 있습니다.
                    <br/>
                    채널 검색 속도를 향상하였습니다.
                    <br/>
                    이제 채널 정보에서 이전 방송 기록을 볼 수 있습니다.
                    <br/>
                    URL 검색 기능을 개선하였습니다.
                    <br/>
                    비디오 목록을 불러올 때 UI가 멈추는 문제를 해결하였습니다.
                    <br/>
                    파일명 템플릿에 resolution(해상도)을 포함한 여러 변수가 추가되었습니다.
                    <br/>
                    이제 Audio Only 해상도는 mp3 파일로 저장됩니다.
                    <br/>
                    비디오 자르기 기능을 개선하였습니다.
                    <br/>
                    네트워크 대기시간이 길어지면 응답 없음 상태가 되는 문제를 해결하였습니다.
                    <br/>
                    일부 길이가 긴 비디오를 다운로드할 때 오류가 발생하는 문제를 해결하였습니다.
                    <br/>
                    소프트웨어 보안 패치를 적용하였습니다.
                    <br/>
                    소프트웨어 최적화 및 안정화를 진행하였습니다.
                </TextGroup>
            </div>
        </div>
    );
};

var SourceUrl = "https://github.com/devhotteok/TwitchLink/releases/tag/" + version;

export default {
    UpdateTitle,
    UpdateNote,
    SourceUrl
}